.App-logo {
    height: 7vmin;
    pointer-events: none;
    position: absolute;
    left: 0;
    stroke: 1px black;
}

.Portfolio-text {
    position: absolute;
    font-size: calc(6px + 2vmin);
    text-shadow: 0 0 3px black;
    top: 0px;
    left: 70px;
}

@media only screen and (max-width: 500px) {
  .Portfolio-text {
    left: 10%;
    top: 12px;
  }
}

.Name-text {
  position: absolute;
  text-shadow: 0 0 3px black;
}
  
@media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
}
  
.App-header-light {
  background-image: radial-gradient( circle 610px at 5.2% 51.6%,  rgba(5,8,114,1) 0%, rgba(7,3,53,1) 97.5% );
    
    min-height: 4vh;
    height: 9vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(2px + 1.5vmin);
    width: 100%;
    text-shadow: 0 0 3px #FF0000;
}
  
.App-header-dark {
  background-image: radial-gradient( circle 610px at 5.2% 51.6%,  rgba(5,8,114,1) 0%, rgba(7,3,53,1) 97.5% );

  min-height: 4vh;
  height: 9vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(2px + 1.5vmin);
  width: 100%;
  text-shadow: 0 0 3px #FF0000;
}

@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}

.Btn-light {
   background-image: linear-gradient(
     to right, #314755 0%, rgba(96,221,142,1) 51%, rgba(24,138,141,1) 100%);
   margin: 1px;
   padding: 4px 10px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color: white;            
   box-shadow: 2px 1px 20px -3px #eee;
   border-radius: 10px;
   display: block;
   position: absolute;
   right: 18px;
   border: none;
}



.Btn-dark {
  background-image: linear-gradient(
    to right, rgba(26,33,64,1) 0%, rgba(81,84,115,1) 51%, #314755  100%);
  margin: 1px;
  padding: 4px 10px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 2px 1px 20px -3px #eee;
  border-radius: 10px;
  display: block;
  position: absolute;
  right: 18px;
  border: none;
}

 .Btn-dark:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }

 .Btn-light:hover {
  background-position: right center; 
  color: #ffffff;
  text-decoration: none;
}

svg {
  height: 4vmin;
  width: auto;
  margin-top: 4.5px;
  transition: width 0.5s, height 0.5s, background-color 0.5s, transform 0.5s;
}

svg:hover {
  cursor: pointer;
  transform: scale(1.1);
}




